<template>
    <div>
        <div class="row">
            <div class="col-md-12">
                <widget headerText="Posts">
                    <data-table :apiUrl="apiUrl"
                                :tableFields="tableFields"
                                :itemsPerPage="itemsPerPage"
                                :sortFunctions="sortFunctions"
                                :apiMode="apiMode"
                                :httpOptions="httpOptions"
                                :paginationPath="paginationPath">
                    </data-table>
                </widget>
            </div>
        </div>
    </div>
</template>

<script>
    import Vue from 'vue'
    import Widget from '../vuestic-components/vuestic-widget/VuesticWidget'
    import DataTable from '../vuestic-components/vuestic-datatable/VuesticDataTable'
    import BadgeColumn from '../tables/BadgeColumn.vue'
    import FieldsDef from './data/fields'
    import ItemsPerPageDef from '../vuestic-components/vuestic-datatable/data/items-per-page-definition'

    Vue.component('badge-column', BadgeColumn)

    export default {
        components: {
            DataTable,
            Widget
        },
        name: 'Table',
        data () {
            return {
                apiUrl: `${window.adminUrl}/post`,
                apiMode: true,
                tableFields: FieldsDef.tableFields,
                itemsPerPage: ItemsPerPageDef.itemsPerPage,
                sortFunctions: FieldsDef.sortFunctions,
                paginationPath: '',
                httpOptions: {
                    withCredentials: true
                }
            }
        }
    }
</script>

<style lang="scss">

    .color-icon-label-table {
        td:first-child {
            width: 1rem;
        }
    }
</style>



